        this.remarkForm_disabled = true;
      },
      showRemark(remark){
        this.last_remark = remark;
        if(this.remarkForm_disabled){
          this.remark = remark;
        }
      },
      setRemark(row){
        let param = {
          uid:row.uid,
          folder:this.$route.params.boxId,
          remark:this.remark
        }
        if(!this.remark){
          this.$message({
            type:'error',
            message:this.lan.REMARK_MSG
          })
          return;
        }
        if(this.remark && this.remark.length>200){
          this.$message({
            type:'error',
            message:this.lan.REMARK_MSG2
          })
          return;
        }
        this.loading = true;
        messageRemark(param).then(res=>{
          this.loading = false;
          this.remarkForm_disabled = true;
          row.remark = this.remark;
          this.$message(
            {type:'success',message: this.lan.COMMON_OPRATE_SUCCESS}
          )
        }).catch(err=>{
          this.loading = false;
          console.log(err)
        })
      },
      cancelRemark(row){
        this.remark = this.last_remark;
        this.remarkForm_disabled = true;
      },
      deleteRemark(row){
        this.$confirm( this.lan.REMARK_DELETE_DESC, this.lan.MAILBOX_COM_READ_SYSTEM_NOTICE, {
          confirmButtonText: this.lan.COMMON_BUTTON_CONFIRM,
          cancelButtonText: this.lan.COMMON_BUTTON_CANCELL,
          type: 'warning'
        }).then(() => {
          this.loading = true;
          let param = {
            uid:row.uid,
            folder:this.$route.params.boxId
          }
          messageRemarkDel(param).then(res=>{
            this.loading = false;
            row.remark = null;
            this.$message(
              {type:'success',message: this.lan.COMMON_DELETE_SUCCESS}
            )
          }).catch(err=>{
            this.loading = false;

          })
        }).catch(() => {

        });

      },
      changeImportFolder(boxId){
        this.$router.push('/mailbox/innerbox/'+boxId)
      },
      cancelUpload(){
        this.$refs.upload_name.uploader.cancel()
      },
      resumeUpload(){
        this.$refs.upload_name.uploader.resume()
      },
      pauseUpload(){
        this.$refs.upload_name.uploader.pause()
      },
      calcMD56(file,callback){
        // this.upstate="MD5...";
        // this.percent=0;
        let chunkSize=2097152,
          chunks=Math.ceil(file.size/chunkSize),
          currentChunk=0,
          spark=new SparkMD5.ArrayBuffer(),
          fileReader=new FileReader();
        fileReader.onload=(e)=>{
          //hash
          spark.append(e.target.result);
          currentChunk++;
          // this.percent=((currentChunk/chunks)*100).toFixed(2)-0;
          if(currentChunk<chunks){
            loadNext();
          }else{
            let str = spark.end();
            callback(str)
            return str;
          }
        }
        //
        function loadNext(){
          let start=currentChunk*chunkSize,
            end=((start+chunkSize)>=file.size)?file.size:start+chunkSize;
          fileReader.readAsArrayBuffer(file.slice(start,end));
        }
        loadNext();
      },
      fileAdded(file){
        let name_index = file.name.lastIndexOf('.');
        if(file.fileType == 'message/rfc822' && file.name.slice(name_index+1) == 'eml'){
          if(file.size==0){
            file.cancel();
            return false;
          }
          let remain = (this.$store.getters.getHomeRemain.capacity_total - this.$store.getters.getHomeRemain.capacity_used)*1024*1024
          if(!remain){
            welcome().then(res=>{
              this.$store.dispatch('setHomeRemainA',res.data.userinfo)
              remain = (res.data.userinfo.capacity_total - res.data.userinfo.capacity_used)*1024*1024
              if(res.data.userinfo.capacity_total>0 &&  file.size > remain){
                let fname = file.name;
                this.$message({
                  type:'error',
                  message:fname+ this.lan.FILE_MAIL_MSG
                })
                file.cancel();
                return false;
              }else{
                this.loading2 = true;
                this.calcMD56(file.file,(a)=>{
                  this.loading2 = false;
                  file.uniqueIdentifier = a;
                  file.resume();
                })
              }
            })
          }else{
            if(this.$store.getters.getHomeRemain.capacity_total > 0 && file.size > remain){
              let fname = file.name;
              this.$message({
                type:'error',
                message:fname+ this.lan.FILE_MAIL_MSG
