      cancelStag(row,flag){
        let param = {
          uids:[row.uid],
          folder:this.$route.params.boxId,
          flag:flag
        }
        cancelStag(param).then(res=>{
          this.$message({
            type:'success',
            message:this.lan.COMMON_OPRATE_SUCCESS
          })
          this.listData_new.forEach(valData=>{
            if( valData.uid==row.uid){
              for(let i=0;i<valData.flags.length;i++){
                if(valData.flags[i] == flag){
                  valData.flags.splice(i,1);
                  for(let k=0;k<valData.tags.length;k++){
                    if(valData.tags[k].tagname == flag){
                      valData.tags.splice(k,1);
                    }
                  }
                }
              }

            }
          })
          // this.getMessageList();
        }).catch(err=>{
          console.log(err)
        })
