          this.view_tag_list = this.viewItems[6].children;

          this.signItems[5].children.push({tag:true,action:'new_add',text:this.lan.CREATE_TAG_ADD,divided:true})
          this.signItems_maillist.push({flags:'cancel_tag',text:this.lan.CANCEL_ALL_TAG,divided:true,action:'cancel_tag'})
