      signHandleCommand:function(item,row){
        this.fullscreenLoading = true;
        if(!item){
          this.fullscreenLoading = false;
          return;
        }
        let param = {
          uids:this.checkedMails,
          folder:this.boxId,
          action:item.action,
          flags:[item.flags]
        }
        if(row && row.uid){
          param.uids = [ row.uid ]
        }
        if(item.action == 'cancel_tag'){//
          let ppa = {
            uids:this.checkedMails,
            folder:this.boxId,
          }
          if(row && row.uid){
            ppa.uids = [ row.uid ]
          }
          cancelTag(ppa).then(res=>{
            this.fullscreenLoading = false;
            if(row && row.uid){
              this.listData_new.forEach(valData=>{
                if(valData.uid && valData.uid==row.uid){
                  valData.flags = [];
                  valData.tags = [];
                }
              })
              return;
            }
            this.checkedMails.forEach(val=>{
              this.listData_new.forEach(valData=>{
                if(valData.uid && valData.uid==val){
                  valData.flags = [];
                  valData.tags = [];
                }
              })
            })
            this.noSelect();
            // this.getMessageList();
            // this.$parent.$parent.$parent.getFloderfn();
          }).catch(err=>{
            console.log(err)
            this.fullscreenLoading = false;
          })
          return;
        }
        if(item.action == 'new_add'){
          this.fullscreenLoading = false;
          this.createFormVisible = true;
          if(row && row.uid){
            this.createForm.uids = row.uid
          }else{
            this.createForm.uids = null;
          }
          return;
        }
        messageFlag(param).then((suc)=>{
          this.fullscreenLoading = false;
          this.$message({
            type:'success',
            message:this.lan.COMMON_OPRATE_SUCCESS
          })
          if(param.flags[0] == '\\Seen'){
            if(param.action == 'add'){ //
              this.checkedMails.forEach(val=>{
                this.listData_new.forEach(valData=>{
                  if(valData.uid && valData.uid==val){
                    console.log(valData)
                    valData.isread = true;
                    if(valData.flagbg_class == 'unseen'){
                      valData.flagbg_class = ''
                      valData.flagStr = this.lan.MAILBOX_COM_INNERBOX_ALREADY_READ
                    }
                  }
                })
              })
            }else if(param.action == 'remove'){ //
              this.checkedMails.forEach(val=>{
                this.listData_new.forEach(valData=>{
                  if(valData.uid && valData.uid==val){
                    console.log(valData)
                    valData.isread = false;
                    if(valData.flagbg_class == ''){
                      valData.flagbg_class = 'unseen'
                      valData.flagStr = this.lan.MAILBOX_COM_INNERBOX_UNREAD
                    }
                  }
                })
              })
            }
            this.$parent.$parent.$parent.getFloderfn();
            this.noSelect();
            return;
          }
          if(param.flags[0] == '\\flagged'){
            if(param.action == 'remove'){ //
              this.checkedMails.forEach(val=>{
                this.listData_new.forEach(valData=>{
                  if(valData.uid && valData.uid==val){
                    valData.flagged = false;
                    valData.color={};
                  }
                })
              })
            }else if(param.action == 'add'){ //
              this.checkedMails.forEach(val=>{
                this.listData_new.forEach(valData=>{
                  if(valData.uid && valData.uid==val){
                    valData.flagged = true;
                    valData.color = {};
                  }
                })
              })
            }
            this.noSelect();
            return;
          }

          if(param.action == 'add' && param.flags[0].indexOf('-')>=0){ //
            this.checkedMails.forEach(val=>{
              this.listData_new.forEach(valData=>{
                if(valData.uid && valData.uid==val){
                  valData.flagged = true;
                  valData.color =  {};
                  let index = param.flags[0].indexOf('-');
                  valData.color['flag'+param.flags[0].slice(index)] = true;
                }
              })
            })
            this.noSelect();
            return;
          }

          if(item && item.tag){//
            if(row && row.uid){
              this.listData_new.forEach(valData=>{
                if(valData.uid && valData.uid==row.uid){
                  let hasTag = false;
                  for(let i=0;i<valData.flags.length;i++){
                    if(valData.flags[i] == param.flags[0]){
                      hasTag = true;
                    }
                  }
                  if(!hasTag){
                    this.taglist.forEach(tagval=>{
                      if(tagval.tagname == param.flags[0]){
                        valData.flags.push(tagval.tagname)
                        valData.tags.push(tagval);
                      }
                    })
                  }
                }
              })
              return;
            }
            this.checkedMails.forEach(val=>{
              this.listData_new.forEach(valData=>{
                if(valData.uid && valData.uid==val){
                  let hasTag = false;
                  for(let i=0;i<valData.flags.length;i++){
                    if(valData.flags[i] == param.flags[0]){
                      hasTag = true;
                    }
                  }
                  if(!hasTag){
                    this.taglist.forEach(tagval=>{
                      if(tagval.tagname == param.flags[0]){
                        valData.flags.push(tagval.tagname)
                        valData.tags.push(tagval);
                      }
                    })
                  }


                }
              })
            })
            this.noSelect();

          }
          // this.getMessageList();
        }).catch(err=>{
          this.fullscreenLoading = false;
          console.log(err)
        })
