//vue
import Vue from 'vue';
import axios from 'axios';

//
import store from '../store/index';

import * as types from '../store/mutation-types';
import cookie from '../assets/js/cookie';
import router from '../router'
import { Loading } from 'element-ui'
import { Message } from 'element-ui';
import { MessageBox  } from 'element-ui';
let needLoadingRequestCount = 0
let loading

const startLoading = function () {
  loading = Loading.service({
      lock: true,
      text: 'Loading',
      spinner: 'el-icon-loading',
      background: 'rgba(255, 255, 255, 0.5)'
    })
}

const endLoading = function () {
  loading.close()
}
const showFullScreenLoading = function () {
  if (needLoadingRequestCount === 0) {
    startLoading()
  }
  needLoadingRequestCount++
}

const tryHideFullScreenLoading = function () {
  if (needLoadingRequestCount <= 0) return
  needLoadingRequestCount--
  if (needLoadingRequestCount === 0) {
    endLoading()
  }
}


let hasBox = false;

// http request 
axios.interceptors.request.use(
  config => {
    if(config.url.indexOf('?')>-1){
      config.url += "tamp="+new Date().getTime();
    }else{
      config.url += "?t="+new Date().getTime();
    }
    if( config.url.indexOf('/setting/secret-reset')>-1){
      return config;
    }
    if( config.url.indexOf('/setting/register')>-1 || config.url.indexOf('/netdisk/netdisks/share')>-1 || config.url.indexOf('/netdisk/netdisks/download-share')>-1){
      return config;
    }
    if(config.url.indexOf('login')==-1 &&!cookie.getCookie('token')){
      store.dispatch('setInfo');
      return;
    }
    if(config.url.indexOf('/netdisk/upload/')<0&&config.url.indexOf('/contact/pab/members')<0){
      // showFullScreenLoading()
    }

    if (cookie.getCookie('token')) {  // tokenhttp headertoken
      config.headers.Authorization = `JWT ${cookie.getCookie('token')}`;
      store.dispatch('setInfo');
    }
    return config;
  },
  err => {
    // router.push('/')
    return Promise.reject(err);
  });

    let messageNotice = null;
// http response 
axios.interceptors.response.use(
  res=>{
    tryHideFullScreenLoading()
    return res;
  },
  error => {

    let res = error.response;
    if(res && res.status){
      switch (res.status) {
      case 401:
        //  401 token
        cookie.delCookie('token');
        store.dispatch('setInfo');

        if(router.currentRoute.path != '/login'){
          if(!hasBox){
            if(store.getters.getNewMsgTimer){clearInterval(store.getters.getNewMsgTimer)}
            let str = '';
            if(res.data && res.data.detail){
              str = res.data.detail
            }
            if(res.data && res.data.non_field_errors) {
              str = res.data.non_field_errors[0]
            }
            hasBox = true;
            MessageBox.alert(str,'',{
              confirmButtonText: '',
              callback: action => {
                hasBox = false;
                router.push('/')
              }
            })
          }
        }

        break;

        case 400:
        if(res.data.non_field_errors){
          Message.error({message:res.data.non_field_errors[0]})
        }
        if(res.data.error){
          Message.error({message:res.data.error})
        }
        if(res.data.detail){
          Message.error({message:res.data.detail})
        }
        if(res.config.responseType == 'blob'){
          var reader = new FileReader();
          reader.onload = function(event){
            var content = reader.result;//
            let obj = JSON.parse(content)
            if(obj.non_field_errors){
              Message.error({message:obj.non_field_errors[0]})
            }
            if(obj.error){
              Message.error({message:obj.error})
            }
            if(obj.detail){
              Message.error({message:obj.detail})
            }
          };
          reader.readAsText(res.data);
        }
        break;
      case 403:
        let detail = '';
        detail = res.data.detail
        if(detail){
          Message.error({message:detail})
        }else{
          // Message.error({message:''})
        }
        console.log('');
        // alert('');
        break;
      case 500:
        // Message.error({message:'~~~'})
        console.log('');
      // alert('');
      }
    }
    tryHideFullScreenLoading()
    //500
    if(res && res.status && res.status>=500 && res.request && res.request.responseURL.indexOf('/mail/message/new/')>=0){
      return Promise.reject(res)
    }
    //wss websocket 500
    if(res && res.status && res.status>=500 && res.request && res.request.responseURL.indexOf('/mail/core/wss/')>=0){
      return Promise.reject(res)
    }
    if(res && res.status && res.status == 500 ){
      if(messageNotice){
        messageNotice.close();
      }
      messageNotice = Message.error({message:''})
      return Promise.reject(res)
    }
    if(res && res.status && res.status > 500){
      if(messageNotice){
        messageNotice.close();
      }
      messageNotice = Message.error({message:''})
      return Promise.reject(res)
    }
    if(error.response && error.response.data){
      return Promise.reject(error.response.data)   // 
    }else{
      return Promise.reject(error.response)
    }
  });




