        })
      },
      screen_time(){
         let time = this.$store.getters.initTime;
         time += 30;
         this.$store.dispatch('setInitTimeA',time)
         console.log(time)
         if(time >= 10*60){
           //  
           clearInterval(this.$store.getters.testUser);
           this.$store.dispatch('settestUserA',null);
           this.$store.dispatch('setInitTimeA',0)
           if(this.$store.getters.isLayout){
              this.heartCheck.reset();
              clearTimeout(this.creat_wstimer)
              this.creat_wstimer = null;
              this.lockReconnect = true;
              if(this.websocket)this.websocket.close();
              this.$store.dispatch('setIsLockA',true)
           }
         }
       },
      eventFun(){
        if(this.$store.getters.isLayout && !this.$store.getters.getIsLock){
           clearInterval(this.$store.getters.testUser);
           this.$store.dispatch('settestUserA',null);
           this.$store.dispatch('setInitTimeA',0)
           this.$store.dispatch('settestUserA',setInterval(this.screen_time,30*1000) );
        }
       },
      lockscreentime(){
        return;
        if(!this.$store.getters.getLoginAfter.is_security){
          return;
        }
         this.$store.dispatch('setIsLayoutA',true)
         clearInterval(this.$store.getters.testUser);
         this.$store.dispatch('settestUserA',null);
         this.$store.dispatch('settestUserA',setInterval(this.screen_time,30*1000) );
         var body = document.querySelector('html');
         body.addEventListener("click",this.eventFun);
         body.addEventListener("keydown",this.eventFun);
         body.addEventListener("mousemove",this.eventFun);
         body.addEventListener("mousewheel",this.eventFun);
