         time += 60;
         this.$store.dispatch('setLockTimeInitA',time)
         console.log(time)
         if(time >= this.$store.getters.getSharedStatus.lock_left){
           //  
           this.$store.dispatch('setLockTimeInitA',0)
           cookie.delCookie('token');
           this.$store.dispatch('setInfo');

           clearTimeout(this.$store.getters.exp_timer);
           this.$store.dispatch('setExpTimerA',null);

           this.heartCheck.reset();
           clearTimeout(this.creat_wstimer)
           this.creat_wstimer = null;
           this.lockReconnect = true;
           if(this.websocket)this.websocket.close();

           let str = this.lan.AXIOS_LOGIN_ERROR;
           if(this.$router.currentRoute.path != '/login'){
             this.$alert(str,this.lan.MAILBOX_COM_READ_SYSTEM_NOTICE,{
                confirmButtonText: this.lan.COMMON_BUTTON_CONFIRM,
                callback: action => {
                  this.$router.push('/login')
                }
             })
           }

         }else{
           this.$store.dispatch('setLockTimerA',setTimeout(this.lock_time,60*1000) );
         }
      },
      lockEventFun(){
         clearTimeout(this.$store.getters.lock_timer);
         this.$store.dispatch('setLockTimerA',null);
         this.$store.dispatch('setLockTimeInitA',0)
         this.$store.dispatch('setLockTimerA',setTimeout(this.lock_time,60*1000) );
      },
      lockInitFn(){
         this.$store.dispatch('setIsLayoutA',true)
         clearTimeout(this.$store.getters.lock_timer);
         this.$store.dispatch('setLockTimerA',null);
         this.$store.dispatch('setLockTimeInitA',0)
         this.$store.dispatch('setLockTimerA',setTimeout(this.lock_time,60*1000) );
         var body = document.querySelector('html');
         body.addEventListener("click",this.lockEventFun);
         body.addEventListener("keydown",this.lockEventFun);
         body.addEventListener("mousemove",this.lockEventFun);
         body.addEventListener("mousewheel",this.lockEventFun);
      },
      exp_time(){
        clearTimeout(this.$store.getters.exp_timer);
        this.$store.dispatch('setExpTimerA',null);
         let time = this.$store.getters.exp_time_init;
         time += 60;
         this.$store.dispatch('setExpTimeInitA',time)
         console.log('exp: '+time)
         if(time >= this.$store.getters.getSharedStatus.expired_left-300){
           //  
           this.$store.dispatch('setExpTimeInitA',0)
           this.refresh();
         }else{
           this.$store.dispatch('setExpTimerA',setTimeout(this.exp_time,60*1000) );
         }
      },
      expInitFn(){
        this.$store.dispatch('setExpTimeInitA',0)
        this.$store.dispatch('setExpTimerA',setTimeout(this.exp_time,60*1000) );
      },
      refresh(){
        refresh({token:cookie.getCookie('token')}).then(res=>{
          cookie.setCookie('token', res.data.token, 7);
          // 
          this.$store.dispatch('setInfo')
          let pbj = this.$store.getters.getSharedStatus;
          pbj.expired_left = 1800;
          this.$store.dispatch('setSharedS',pbj)
          this.expInitFn();
        }).catch(err=>{
          console.log(err);
        })
      },
      close_new_msg(){
        this.newList = [];
        $('title').text(this.$store.getters.getLoginAfter.title)
      },
      created_websoket(){
        clearTimeout(this.$store.getters.wssTimer);
        let _this = this;
        getWss().then(res=>{
          // console.log('create_websocket')
          if(location.origin.indexOf('https://') == 0){
            this.wsUrl = location.origin.replace('https','wss') + '/websocket?token='+res.data.token
          }else{
            this.wsUrl = location.origin.replace('http','ws') + '/websocket?token='+res.data.token
          }
          // this.wsUrl = 'ws://192.168.1.39/websocket?token='+res.data.token
          this.websocket = new WebSocket(this.wsUrl);

          //
          this.heartCheck = {
              timeout: 60000,//60
              timeoutObj: null,
              serverTimeoutObj: null,
              reset: function(){
                // console.log('reset')
                  clearTimeout(this.timeoutObj);
                  this.timeoutObj = null
                  clearTimeout(this.serverTimeoutObj);
                  this.serverTimeoutObj = null;
                  return this;
              },
              start: function(){
                  var self = this;
                  self.timeoutObj = setTimeout(function(){
                      //
                      //onmessage
                      // console.log('ping')
                      _this.websocket.send("Ping");
                      self.serverTimeoutObj = setTimeout(function(){//
                          _this.websocket.close();//onclosereconnectws.close().reconnect onclose
                      }, self.timeout)
                  }, this.timeout)
              }
          }

          /**/
          this.websocket.onopen = function () {
              // send()
              // var data = ""
              // var data = JSON.stringify({token: cookie.getCookie('token')})
              // _this.websocket.send(data);
              _this.heartCheck.reset();
              _this.heartCheck.start();
          };
          /**/
          this.websocket.onmessage = function (event) {
              _this.heartCheck.reset();
              _this.heartCheck.start();
              var edata = event.data;
              var data = JSON.parse(edata);
              if (data.result){
                _this.newList.unshift(data.result)
                $('title').text(_this.lan.LAYOUT_INDEX_YOU_HAVE +_this.newList.length+ _this.lan.TITLE_DESC)
              }
          };
          /**/
          this.websocket.onerror = (evt,e) => {
            // console.log('')
            _this.reconnect();
          }
          /**/
          this.websocket.onclose = evt => {
            // console.log('')
            _this.reconnect();
          };
        }).catch(err=>{
          console.log(err)
          this.heartCheck.reset();
          clearTimeout(this.creat_wstimer)
          this.creat_wstimer = null;
          this.lockReconnect = true;
          if(this.websocket)this.websocket.close();
          if(err && err.status>=500){
            this.$store.dispatch('setWssTimerA',setTimeout(this.created_websoket,30000))
          }
        })
      },
      reconnect() {
          if(this.lockReconnect) return;
          // console.log('')
          this.lockReconnect = true;
          //
          clearTimeout(this.creat_wstimer)
          this.creat_wstimer = null;
          this.creat_wstimer = setTimeout( ()=> {
              this.created_websoket();
              this.lockReconnect = false;
          }, 2000);
      },
      goSetPassword(){
