      title_scroll(){
        let _this = this
        function func(){
          let  s= $('title').text().split("");
          if(s.join("") == _this.$store.getters.getLoginAfter.title){
            clearInterval(this.title_scroll_timer)
            return;
          }
            s.push(s[0]); //
            s.shift();// 
            $('title').text(s.join(""))
