          // {id:4,title:'',iconclass:'icon-iconmore'},
          {id:6,title:lang.LAYOUT_INDEX_SELF_QUERY,iconclass:'icon-iconsreachm'},
        ]
        return lang


      },
      check_passwd_size2(){
        let value = this.passwordForm.new_password
        if(value === '')return false;
        if(value.length>16 || value.length<this.passwordRules.passwd_size2){
          return false;
        }
        return true;
      },
      check_passwd_type_2(){
        let value = this.passwordForm.new_password;
        if(value === '')return false;
        let reg=/^(?![0-9]+$)(?![a-z]+$)(?![A-Z]+$)(?!([^(0-9a-zA-Z)])+$)^.{1,16}$/;
        if(!reg.test(value)){
          return false;
        }
        return true;
      },
      check_passwd_type_3(){
        let value = this.passwordForm.new_password;
        if(value === '')return false;
        let reg=/^(?![a-zA-Z]+$)(?![A-Z0-9]+$)(?![A-Z\W_!@#$%^&*`~()-+=.]+$)(?![a-z0-9]+$)(?![a-z\W_!@#$%^&*`~()-+=.]+$)(?![0-9\W_!@#$%^&*`~()-+=.]+$)[a-zA-Z0-9\W_!@#$%^&*`~()-+=.]{1,16}$/;
        if(!reg.test(value)){
          return false;
        }
        return true;
      },
      check_passwd_type_4(){
        let value = this.passwordForm.new_password;
        if(value === '')return false;
        let reg=/^(?=.*?[a-z])(?=.*?[A-Z])(?=.*?\d)(?=.*?[_!@#$%^&*`~()-+=.])[a-zA-Z\d_!@#$%^&*`~()-+=.]*$/
        if(!reg.test(value)){
          return false;
        }
        return true;
      },
      check_passwd_digital(){
        let value = this.passwordForm.new_password;
        if(value === '')return false;
        let num = '0123456789'
        let arr = [];
        for(let i=1;i<num.length-1;i++){
          arr.push(num.slice(i-1,i+2));
        }
        let num_re = '9876543210'
        for(let i=1;i<num_re.length-1;i++){
          arr.push(num_re.slice(i-1,i+2));
        }
        let reg = new RegExp(arr.join('|'))
        if(reg.test(value)){
          return false;
        }
        return true;
      },
      check_passwd_letter(){
        let value = this.passwordForm.new_password;
        if(value === '')return false;
        let abc = 'abcdefghijklmnopqrstuvwxyz'
        let arr = [];
        for(let i=1;i<abc.length-1;i++){
          arr.push(abc.slice(i-1,i+2));
        }
        let abc_re = 'zyxwvutsrqponmlkjihgfedcba'
        for(let i=1;i<abc_re.length-1;i++){
          arr.push(abc_re.slice(i-1,i+2));
        }
        let reg = new RegExp(arr.join('|'))
        if(reg.test(value.toLowerCase())){
          return false;
        }
        return true;
      },
      check_passwd_letter2(){
        let value = this.passwordForm.new_password;
        if(value === '')return false;
        let reg = /(\w)*(\w)\2{2}(\w)*/;
        if(reg.test(value.toLowerCase())){
          return false;
        }
        return true;
      }

    },
    beforeRouteLeave (to, from , next) {
      this.heartCheck.reset();
      clearTimeout(this.creat_wstimer)
      this.creat_wstimer = null;
      this.lockReconnect = true;
      if(this.websocket)this.websocket.close();

