      this.$store.dispatch('setIsLayoutA',false)

      clearTimeout(this.$store.getters.wssTimer);
      clearTimeout(this.$store.getters.lock_timer);
      clearTimeout(this.$store.getters.exp_timer);

      next();

    }
  }
