<template>
    <div class="u-screenlock">
  <div class="c-mask c-mask-img">
    <!--<img src="89517/style/img/scrbg.jpg">-->
  </div>
  <div class="c-mask c-mask-color">

  </div>
  <div class="c-main">
    <div class="c-header skin-primary-bg">&nbsp;</div>
    <div class="c-content" v-loading="loading">
      <h1>{{lan.LOCKSCREEN_TITLE}}</h1>

      <div class="c-content-icon">
        <i></i>
      </div>

      <div class="c-content-form">
        <div class="u-form-item">
          <label>
            {{lan.LOCKSCREEN_MAILBOX_PASSWORD}}
          </label>
          <el-input type="text"  v-model="lockpassword_input" @keyup.native="$trans_pwd('lockpassword_input','lockpassword')"></el-input>
          <el-input type="hidden"  v-model="lockpassword" v-show="false"></el-input>
        </div>

        <div class="u-form-item">
          <button class="u-btn u-btn-primary j-submit u-screenlock-submit" type="button" @click="lockscreenfn">{{lan.COMMON_BUTTON_CONFIRM}}</button>
        </div>

        <p class="u-form-explain f-dn j-explain"></p>
      </div>

    </div>
    <div class="c-footer" v-if="false">
      <ul class="u-list u-list-horizontal">
        <!--<li><a href="#" target="_blank" ></a></li>-->
      </ul>
    </div>
  </div>
</div>
</template>

<script>
  import lan from '@/assets/js/lan';
import {lockscreen} from '@/api/api'
import cookie from '@/assets/js/cookie';
import router from '@/router'
export default {
    data(){
        return {
            lockpassword:'',
            lockpassword_input:'',
            loading:false
        }
    },
    methods:{
        keyupEnter(){
            document.onkeydown = e =>{
                if (e.keyCode === 13 && this.$store.getters.getIsLock) {
                   this.lockscreenfn();
                }
            }
        },
        lockscreenfn(){
            var that =this;
            if(this.lockpassword.length==0){
              this.$message({
                type:'error',
                message:this.lan.LAYOUT_INDEX_PASSWORD_RULE
              })
              return;
            }
            this.loading = true;
            lockscreen({"password":this.$get_enc_pwd(this.lockpassword)})
            .then((response)=>{
              this.loading = false;
              if(response.data.token){
                var token = response.data.token;
                //
                cookie.delCookie('locked')
                cookie.setCookie('token',response.data.token,7);
                that.$store.dispatch('setInfo');
                this.lockpassword = '';
                // that.$router.push(that.$store.state.lastUrl)
                that.$store.dispatch('setIsLockA',false)
                if(this.$root.$children[0].$refs.layoutBlock){
                  this.$root.$children[0].$refs.layoutBlock.lockscreentime();
                  this.$root.$children[0].$refs.layoutBlock.created_websoket();
                }
              }
            }).catch(err=>{
              this.loading = false;
              console.log(err)
              this.$message.error(this.lan.MAILBOX_COM_READ_PASSWORD_ERROR);
                // this.open('');
            });
        }
    },
    mounted:function(){
      cookie.setCookie('locked','1',7);
      this.$store.dispatch('setInfo');
    },
    computed:{
      lan:function(){
        let lang = lan.zh
        if(this.$store.getters.getLanguage=='zh-hans'){
          lang = lan.zh
        }else if(this.$store.getters.getLanguage=='zh-tw'){
          lang = lan.zh_tw
        }else if(this.$store.getters.getLanguage=='en'){
          lang = lan.en
        }else if(this.$store.getters.getLanguage=='es'){
          lang = lan.zh
        }else{
          lang = lan.zh
        }
        return lang

      }
    },
    created(){
      this.keyupEnter();
      let aa = this.$root.$children[0].$refs.layoutBlock
      aa.heartCheck.reset();
      clearTimeout(aa.creat_wstimer)
      aa.creat_wstimer = null;
      aa.lockReconnect = true;
      if(aa.websocket)aa.websocket.close();

      clearInterval(this.$store.getters.testUser);
      clearTimeout(this.$store.getters.wssTimer);
      this.$store.dispatch('settestUserA',null);
      this.$store.dispatch('setInitTimeA',0)
    }
}
</script>
<style>
.c-content-icon>i{
    display:inline-block;
    width:62px;
    height:62px;
    background:url('../../assets/img/iconlock.png') no-repeat;
}
</style>




