import { JSEncrypt } from 'jsencrypt'

Vue.prototype.$get_enc_pwd = function(pwd,time){
  let PUBLIC_KEY = "-----BEGIN PUBLIC KEY-----\n"+
"MIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQCiEHBqb9VWiA+Hnqd8k+/BSTPX\n"+
"F5AOA9rRgppSNtgqjLNOSqFORY/4h0NNZn8u0qD2y1oZTLaNzgzSIdP2CXRpnqoq\n"+
"rvCy64iecF2CoK9fIGs856ndNja4zZhXJXHiUDDEIY3OEOBVuCK4nv1olfvJ4UGX\n"+
"Mi/Lxev03xkYDjdFEQIDAQAB\n"+
"-----END PUBLIC KEY-----";
  let encrypt = new JSEncrypt();
  encrypt.setPublicKey(PUBLIC_KEY);
  if(!time){
    time = Math.floor(new Date().getTime()/1000);
  }
  let keys = 'p='+encodeURIComponent(pwd)+'&s='+time;
  // console.log(keys)
  let encrypted = encrypt.encrypt(keys);
  // console.log( encrypted);
  return encrypted
}
Vue.prototype.$trans_pwd = function(inputpwdname,lastpwdname){
  let inputpwd = this[inputpwdname];
  let lastpwd,lastpwdname_arr;
  let hasDot = lastpwdname.indexOf('.')>=0;
  if(hasDot){
    lastpwdname_arr = lastpwdname.split('.');
    lastpwd = this[lastpwdname_arr[0]][lastpwdname_arr[1]];
  }else{
    lastpwd = this[lastpwdname];
  }
  let h_len = lastpwd.length;
  let o_len = inputpwd.length;
  if(o_len>h_len){
    if(hasDot){
      this[lastpwdname_arr[0]][lastpwdname_arr[1]] = lastpwd+inputpwd.slice(inputpwd.length-(o_len-h_len))
    }else{
      this[lastpwdname] = lastpwd+inputpwd.slice(inputpwd.length-(o_len-h_len))
    }
  }else if(o_len < h_len){
    if(hasDot){
      this[lastpwdname_arr[0]][lastpwdname_arr[1]] = lastpwd.slice(0,o_len)
    }else{
      this[lastpwdname] = lastpwd.slice(0,o_len)
    }
  }
  let str = '';
  for(let i=0;i<inputpwd.length;i++){
      str += '*'
  }
  this[inputpwdname] = str;
}

